<template>
    <div>
        <basic-header :items="['用户管理','用户查看']"></basic-header>
        <el-card>
            <el-tabs v-model="activeName">
                <el-tab-pane name="list" label="用户查看">
                    <el-form :inline="true">
                        <el-form-item label="昵称">
                            <el-input v-model="searchValue"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button circle icon="el-icon-search" type="primary" @click="search"></el-button>
                        </el-form-item>
                    </el-form>
                    <el-table :data="tableData">
                        <el-table-column label="id" prop="id"></el-table-column>
                        <el-table-column label="昵称" prop="nickName"></el-table-column>
                        <el-table-column label="性别" prop="gender">
                            <template #default="o">
                                <span v-if="o.row.gender===1">男</span>
                                <span v-else>女</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="头像" prop="avatarUrl">
                            <template #default="o">
                                <img :src="o.row.avatarUrl" alt="" width="30" height="30">
                            </template>
                        </el-table-column>
                        <el-table-column label="创建时间" prop="create_time"></el-table-column>
                    </el-table>
                    <el-pagination
                            background
                            style="margin-top:30px;"
                            layout="prev,pager,next"
                            :total="total"
                            :page-size.sync="pageSize"
                            :current-page.sync="page"
                            @current-change="change"
                    ></el-pagination>
                </el-tab-pane>
            </el-tabs>
        </el-card>
    </div>
</template>
<script>
    import Header from "@/components/Header.vue"
    export default {
        data(){
            return {
                activeName: "list",
                tableData: [],
                page: 1,
                pageSize: 5,
                total: 0,
                api: "/api/users/users",
                searchValue:""
            }
        },
        methods: {
            fetchData(){
                this.axios.get(this.api, {
                    params: {
                        page: this.page,
                        pageSize: this.pageSize,
                        nickName:this.searchValue
                    }
                }).then(res => {
                    if (res.data.code === 200) {
                        this.tableData=res.data.data;
                        this.total=res.data.total;
                    }
                })
            },
            change(){
                this.fetchData();
            },
            search(){
                this.page=1;
                this.fetchData();
            }
        },
        components: {
            "basic-header": Header
        },
        created(){
            this.fetchData();
        },
        mounted(){

        }
    }
</script>
